import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

import { BrowserRouter } from 'react-router-dom';

import { Provider } from 'react-redux';
import { combineReducers, createStore } from 'redux';
import { scryRenderedComponentsWithType } from 'react-dom/cjs/react-dom-test-utils.development';


let alert = true;

function reducer2(state=alert, action) {

  if(action.type == "close"){
    let newAlert = state;
    newAlert=false;
    return newAlert;
    
  }else{
    return alert;
  }

}


let defaultState = [
  { id : 0, name : 'White and Black', quan : 1}
];

function reducer(state = defaultState , action) {
  if(action.type === 'goodsAdd'){
    let copy = [...state];
    let exist = copy.find((v)=>{
      return v.id === action.payload.id;
    });
    if(exist) {
      copy[action.payload.id].quan++;

    }else{
      copy.push(action.payload);
    }
    
    return copy;

  }else if(action.type === 'plus' ){

    let copy = [...state];
    copy[action.id].quan++;
    return copy;

  }else if(action.type === 'minus'){

    let copy = [...state];
    if(copy[action.id].quan > 0 ) copy[action.id].quan--;

    return copy;

  }else {

    return state;

  }
}

let store = createStore(combineReducers({reducer,reducer2}));


ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
